import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import iView from 'iview'
import 'iview/dist/styles/iview.css'
import Axios from 'axios'

const URL = process.env.NODE_ENV === 'production' && process.env.VUE_APP_TITLE !== 'experiment' ? 'https://api.psy-1.com' : 'https://api.debug.psy-1.com'

declare module 'vue/types/vue' { // 模块补充 可以去这个路径查阅
  interface Vue {
    url: string,
    qiniuToken: any,
    load: boolean,
    $axios: any,
    [key:string]: any // 解决对象的遍历
  }
}

const myAxios = Axios.create({
  baseURL: URL
})

Vue.use(iView)
Vue.config.productionTip = false
Vue.prototype.$axios = myAxios

new Vue({
  data () {
    return {
      url: process.env.NODE_ENV === 'production' ? process.env.VUE_APP_TITLE === 'experiment' ? 'http://web.debug.yetaowang.com/cosleep/evaluate' : 'http://www.yetaowang.com/cosleep/evaluate' : 'http://192.168.88.33:8083',
      // process.env.NODE_ENV === 'production' ? 'http://www.yetaowang.com/cosleep/evaluate' : 'http://192.168.88.33:8083', // 'http://web.debug.yetaowang.com/cosleep/evaluate', // 'http://web.debug.yetaowang.com/cosleep/evaluate',
      load: false,
      qiniuToken: ''
    }
  },
  methods: {
  },
  router,
  store,
  created () {
    this.$axios.get('/evaluate-miniapp/upload/token').then((res: any) => {
      console.log(res.data.data.token)
      this.qiniuToken = res.data.data.token
    })
  },
  mounted () {
    // console.log(this.$router)
  },
  render: h => h(App)
}).$mount('#app')
