import { mixins } from 'vue-class-component'
import wjhJS from '../util/wjhJS.js'
import Design from '@/components/design.vue' // @ is an alias to /src
import uploadPic from '../components/uploadPic.vue'

// 模块管理的
@Component
class adTemplateMtehods extends Vue {
  @Provide() typeList: object[] = [{ label: '推荐素材 +', value: 'recommendMaterial' }, { label: '随机素材 +', value: 'randomMaterial' }, { label: '置顶素材 +', value: 'stickMaterial' }] //  { label: '准不准反馈 +', value: '' }, { label: '分享组件 +', value: '' }
  adName :string = '' // 名称

  adSelect: number = 0
  adSelectOptions: object[] = [{ label: '微信', value: 0 }, { label: 'app', value: 1 }, { label: '同时展示', value: 2 }]

  adTableData: object[] = []
  // 修改adTableData 为computed

  advertisingTemplateShow (type: string = 'normal') {
    this.editType = type
    this._ref('design').show = true
    this.adName = ''
    this.adTableData = []
  }

  recommendMaterialLength: number = 0 // recommendMaterial 的id记录
  randomMaterial: number = 0 // randomMaterial 的id记录
  stickMaterialLength: number = 0
  // 添加列表
  adAdd (type: string) {
    let data: object = {}

    switch (type) {
      case 'recommendMaterial':
        this.recommendMaterialLength += 1
        data = {
          id: '',
          platForm: 0,
          selfId: this.recommendMaterialLength
        } as recommendMaterialData
        break
      case 'randomMaterial':
        this.randomMaterial += 1
        data = {
          selfId: this.randomMaterial
        } as randomMaterialData
        break
      case 'stickMaterial':
        this.stickMaterialLength += 1
        data = {
          id: '',
          platForm: 0,
          selftId: this.stickMaterialLength
        }
        break
    }
    this.adTableData.push(
      {
        typeName: type,
        data,
        random: (Math.random() + '').slice(0, 5)
      } as adTable
    )
    const bf = this.adTableData.filter((val: any) => {
      return val.typeName === 'stickMaterial'
    })
    const aft = this.adTableData.filter((val: any) => {
      return val.typeName !== 'stickMaterial'
    })
    this.adTableData = bf.concat(aft)
  }

  // 模板中最后提交按钮的事件
  editId: number = 0
  editType: string = 'normal'
  adEvent () {
    if (this.editType === 'edit') {
      this.adEdit(this.editId)
      return
    }
    this.submitadData()
  }

  // 修改数据
  adEdit (id: any) {
    this.$axios.post('/evaluate-miniapp/ad/module/edit', { id, name: this.adName, data: JSON.stringify(this.adTableData) }).then((res: object) => {
      console.log(res)
      this.getadData()
      this._ref('design').show = false
    })
  }

  // 删除列表
  adDelete (index: number, type: string) {
    if (this.adTableData.length === 0) {
      return
    }
    this.adTableData.splice(index, 1)
  }

  // 查看数据  没有返回对应的name
  readAdData (id: number, name: string) {
    this.$axios.get('/evaluate-miniapp/ad/module/read', { params: { id } }).then((res: any) => {
      this.editId = id
      this.advertisingTemplateShow('edit')
      const data = res.data.data[0]
      console.log(data, name)
      this.adName = name
      this.adTableData = data
    })
  }

  // 获取展示table的数据 data1
  getadData () {
    this.data1 = [] // 清空
    this.$axios.get('/evaluate-miniapp/ad/module').then((res: any) => {
      const data = res.data.data || []
      for (let i of data) {
        console.log(i)
        this.data1.push({
          id: i.id,
          illustrate: i.illustrate,
          createTime: i.createTime,
          name: i.name
        })
      }
    })
  }

  // 删除column1
  deleteAdColumn (id: number, index:any) {
    this.$axios.post('/evaluate-miniapp/ad/module/del', { id }).then((res: any) => {
      console.log(res)
      this.data1.splice(index, 1)
    })
  }

  // 提交数据
  submitadData () {
    console.log(this.adName)
    console.log(this.adTableData)
    this.$axios.post('/evaluate-miniapp/ad/module', { name: this.adName, data: JSON.stringify(this.adTableData) }).then((res :object) => {
      console.log(res)
      this.getadData()
      this._ref('design').show = false
    })
  }

  mounted () {
    this.getadData()
  }
}

// 素材管理的
@Component({
  components: {
    uploadPic
  }
})
class materialTemplateMtehods extends mixins(adTemplateMtehods) {
  @Provide() materialList: object[] = [{ label: '添加素材 +', value: 0 }]
  single:boolean = false // 测试所用 可删除
  value2 = '' // 添加模板的时候作则的东西
  materialTable: any[] = []
  materialTemplateShow () {
    this._ref('design1').show = true
  }

  // 图片上传
  materialUrlChange (rdx: any, res: string) {
    this.materialTable[rdx].data.picUrl = res
  }

  // 添加
  materialAdd (type: string | number) {
    let data: object = {}
    switch (type) {
      case 0:
        data = {
          picUrl: '',
          link: '',
          name: ''
        } as material0Data
        break
    }
    this.materialTable.push({
      typeName: type,
      data,
      random: (Math.random() + '').slice(0, 7)
    } as materialTable)
  }

  // 删除
  materialDelete (index: number) {
    this.materialTable.splice(index, 1)
  }

  // 获取素材列表
  materialGetTable () {
    this.$axios.get('/evaluate-miniapp/material').then((res: any) => {
      const data:any[] = res.data.data || []
      for (let i of data) {
        console.log(i.data)
        this.material.push({
          id: i.id,
          createTime: i.createTime,
          data: i.data as material0Data,
          typeName: i.typeName,
          isRandom: i.isRandom
        })
      }
    })
  }

  busy:boolean = false // 无奈的办法 赶时间 后面再想
  // 改变是否进入随机素材库
  changeToRandom (isRandom: boolean, id: number) {
    if (this.busy) {
      setTimeout(() => {
        this.busy = false
      }, 200)
      return
    }
    this.busy = true
    this.$axios.post('/evaluate-miniapp/material/israndom', { isRandom: !isRandom, id }).then((res: object) => {
      console.log(res)
    })
  }

  // 提交素材
  materialSubmit () {
    console.log(this.materialTable)
    this.$axios.post('/evaluate-miniapp/material', { data: JSON.stringify(this.materialTable) }).then((res: any) => {
      console.log(res)
    })
  }
}

